Link:hover {
  cursor: pointer;
}

.navContainer {
  width: 100%;
  border: 1px solid #b5b5b5;
  background-color: #d8d8d8;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 20px;
}

.navHeader {
  text-align: center;
  font-size: 3em;
  color: #4a4a4a;
  margin-top: 5px;
  margin-bottom: 30px;
}

.navButtonContainer {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-item-align: center;
      align-self: center;
}

.navButton {
  display: block;
  height: 55px;
  width: 245px;
  min-width: 245px;
  background-color: #2bc0c3;
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  border: 1.3px solid #95a1a6;
  margin-bottom: 25px;
  margin-left: 15px;
}

@media screen and (min-width: 600px) {
  .navButtonContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
  }
}

@media screen and (min-width: 1080px) {
  .navContainer {
    min-height: 100vh;
    width: 320px;
  }
  .navHeader {
    text-align: left;
  }
  .navButtonContainer {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .navButton {
    -ms-flex-item-align: auto;
        align-self: auto;
    margin-left: 0;
  }
}



